let _self
import {uniPopup} from '@dcloudio/uni-ui'
import {popup} from '../components/popup.vue'
export default {
	data() {
		return {
			title:'',
			content:'',
			authorName:'我是小袁',
			time:'今天 09:23:32',
			commentList:[],
			publishComment:''
		}
	},
	components:{
		popup
	},
	onLoad(e) {
		_self = this;
		console.log(',')
		let type = e.type
		let listIndex = e.listIndex
		_self.getNewsData(type,listIndex)
		
	},
	methods: {
		getNewsData:function(type,listIndex){
				uni.request({
					url:'http://47.97.213.36:7300/mock/5e145ffaf2ecc141ee032ab8/leader/newsDetail',
					method:'POST',
					data:{
						type: type,
						listIndex: listIndex
					},
					success:function(res){
						console.log(res.data)
						_self.title = res.data.data.newsDetail.title
						_self.content = res.data.data.newsDetail.content
						_self.commentList = res.data.data.comment
					},
					fail:function(){
						
					}
				})
		},
		publish:function(){
			/* _self.$refs.myPopup.open({
				type:'success',
				content:'发表成功',
				timeout:2000,
			}) */
			uni.showToast({
				title:'发表成功'
			})
			_self.publishComment = ''
			uni.hideKeyboard()
		}
	}
}
